@import url('https://fonts.googleapis.com/css?family=Josefin+Slab|Ubuntu');
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-intro {
  font-size: large;
}

.Budget {
    margin: auto;
}

h1 {
  text-align: center;
  font-size: 50px;
  color: white;
}

.OverallBalance-input {
  width: 300px;
  font-family: 'Josefin Slab', serif;
  font-size: 50px;
  border-radius: 20px;
  background-color: #1eb550;
  color: white;
    border-color: white;
}

.OverallBalance-submit {
  background-color: darkgreen;
  color: white;
  font-family: 'Josefin Slab', serif;
    font-weight: bold;
    letter-spacing: 0.5px;
  height: 35px;
  width: 60px;
  border-radius: 15px;
    border-color: black;
    margin-top: 1vh;

}

.popup {
    border-color: black;
    font-family: 'Josefin Slab', serif;
    font-weight: bold;
    background-color: white;
    text-align: center;
    height: 25vh;
    width: 25vw;
    margin: auto;
    border-radius: 15px;
}

.popup input {
    font-family: 'Josefin Slab', serif;
    display: inline-block;
}

.popup input:focus {
    background-color: lightblue;
}

.updateClose {
    background-color: darkgreen;
    color: white;
    font-family: 'Josefin Slab', serif;
    font-weight: bold;
    letter-spacing: 0.5px;
    height: 50px;
    width: 80px;
    border-radius: 15px;
    border-color: black;
}

.CategoryButton {
    text-align: left;
    margin-top: 2vh;
    padding-top: 1vh;
    padding-bottom: 1vh;
    font-family: 'Josefin Slab', serif;
    font-size: 30px;
    border-radius: 20px;
    color: white;
    border-color: white;
    float: left;
    margin-left: 1vw;
    margin-right: 3vw;
    margin-bottom: 1vw;
}

.BudgetTitle {
    text-align: center;
    padding-top: 1vh;
    padding-bottom: 1vh;
    font-family: 'Josefin Slab', serif;
    font-size: 20px;
    font-weight: bold;
    border-radius: 5px;
    background-color: #609dff;
    color: white;
    float: left;
    clear: left;
    margin-left: 1vw;
}

.Entry {
    text-align: center;
    padding-top: 1vh;
    padding-bottom: 1vh;
    font-family: 'Josefin Slab', serif;
    font-size: 20px;
    font-weight: bold;
    border-radius: 5px;
    background-color: #609dff;
    color: white;
    float: left;
    //clear: left;
}

.CategoryBalance {
    text-align: center;
    padding-top: 0vh;
    padding-bottom: 1vh;
    font-family: 'Josefin Slab', serif;
    font-size: 20px;
    font-weight: bold;
    border-radius: 5px;
    background-color: #609dff;
    color: white;
    float: left;
}





